<template>
  <div>
    <Header/>
    <el-container class="main-container">
      <el-main>

        <div class="list-content" style="margin-bottom: 20px;">
          <div class="con-header">
            <el-row type="flex">
              <el-col v-if="nodeData.name === ''"><img src="@/assets/img/exchange2.png" alt=""> {{ $t('lang.dpos_node_detail_title') }} </el-col>
              <el-col v-else><img src="@/assets/img/list.png" alt=""> {{ nodeData.name}} </el-col>
              <el-col>{{ nodeid }}</el-col>
            </el-row>
          </div>
        </div>

        <div class="list-content">
          <div class="con-title">
            {{ $t('lang.dpos_node_detail1') }}
          </div>
          <el-table
            :data="tableData"
            style="">
            <el-table-column
              prop="address"
              :label="$t('lang.dpos_node_detail2')"
              width="550">
            </el-table-column>
            <el-table-column
              prop="entrust_amount"
              :label="$t('lang.dpos_node_detail3')"
              width="">
            </el-table-column>
            <el-table-column
              prop="incoming"
              :label="$t('lang.dpos_node_detail4')">
            </el-table-column>
            <el-table-column
              prop="unpaid"
              :label="$t('lang.dpos_node_detail5')">
            </el-table-column>
          </el-table>
        </div>
      </el-main>
    </el-container>
    <Footer/>
  </div>
</template>

<script>
    import Header from '@/components/Header'
    import Footer from '@/components/Footer'
    import pageSize from '@/components/Page'

    export default {
        components: {
            Header,
            Footer,
            pageSize
        },
        data() {
            return {
                tableData: [],
                nodeData: [],
                nodeid: ''
            }
        },
        created() {
            this.nodeid = this.$route.query.nodeid;
        },
        computed: {},
        mounted() {
            this.getNodeUserList(this.nodeid)
        },
        methods: {
            getNodeUserList(nodeid){
                this.$http.get("api/node/userlist", {nodeid: nodeid}).then(res => {
                    if (res.data.code === 200){
                        this.tableData = res.data.data.list
                        this.nodeData = res.data.data.node
                    }
                })
            }
        }
    }


</script>
<style>
  .el-main {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: center;
  }
  .el-main .list-content {
    width: 1200px;
    min-width: 1200px;
  }


  .el-main .list-content .con-header {
    height: 130px;
    background-image: url(../../assets/img/trade_bg.png);
    background-size: 100% 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
  }
  .el-main .list-content .con-header img {
    width: 28px;
    height: 28px;
    margin-right: 10px;
  }
  .el-main .list-content .con-header .el-row {
    width: 100%;
    height: 130px;
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: center;
  }

  .el-main .list-content .con-header .el-row .el-col {
    width: 100%;
    display: flex;
    align-items: center;
    font-size: 24px;
    font-weight: 400;
    color: rgba(255, 255, 255, 1);
    padding-left: 18px;
  }

  .el-main .list-content .con-header .el-row .el-col:last-child {
    font-size: 18px;
    margin-top: 26px;
  }


  .el-main .list-content .con-title {
    height:60px;
    background:rgba(243,244,247,1);
    border-radius:4px 4px 0 0;
    font-size:16px;
    font-weight:400;
    color:rgba(0,0,0,1);
    line-height:18px;
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 0 27px;
  }
  .el-main .list-content .el-table {
    padding: 0 27px;
  }
  .el-main .list-content .el-table tr {
    height: 60px;
    padding: 0 27px;
    width: 1146px;
  }
  .el-main .list-content .el-table thead .cell {
    font-size:16px;
    font-weight:400;
    color:rgba(0,0,0,1);
    padding: 0;
  }
  .el-main .list-content .el-table tbody .cell {
    font-size:14px;
    font-weight:400;
    color:rgba(0,0,0,1);
    padding: 0;
  }
</style>




