<template>
  <div class="mobile-container">
    <Header/>

    <div class="m-content">
        <div class="m-head">
            <div class="m-item">
                <div class="item-lebel">{{ blockData.supply }} GKC</div>
                <p>{{ $t('lang.index_title_supply') }}</p>
            </div>
            <div class="m-item">
                <div class="item-lebel">{{ blockData.height }}</div>
                <p>{{ $t('lang.index_title_height') }}</p>
            </div>
            <div class="m-item">
                <div class="item-lebel">{{ blockData.masternodes }}</div>
                <p>{{ $t('lang.index_title_dpos') }}</p>
            </div>
            <div class="m-item">
                <div class="item-lebel">{{ blockData.node }}</div>
                <p>{{ $t('lang.index_title_masternodes') }}</p>
            </div>
        </div>
        <div class="m-tips">
          {{ $t('lang.index_detail_title') }}
        </div>
      <div>
        <el-table
          :data="tableData"
          style="">
          <el-table-column
            prop="block"
            :label="$t('lang.index_table_title1')"
            width="90">
            <template slot-scope="scope">
              <router-link tag="a" :to="{path:'/mblock',query:{height:scope.row.block}}">{{ scope.row.block }}
              </router-link>
            </template>
          </el-table-column>
          <el-table-column
            prop="approx_time"
            :label="$t('lang.index_table_title2')"
            width="230">
            <template slot-scope="scope">
              {{ filterDate(scope.row.approx_time) }}
            </template>
          </el-table-column>
          <el-table-column
            prop="transactions"
            :label="$t('lang.index_table_title3')"
            align="center"
            width="120">
          </el-table-column>
          <el-table-column
            prop="value_out"
            :label="$t('lang.index_table_title4')"
            width="150">
          </el-table-column>
          <el-table-column
            prop="difficulty"
            :label="$t('lang.index_table_title5')"
            width="150">
          </el-table-column>
          <el-table-column
            prop="average_age"
            :label="$t('lang.index_table_title6')"
            width="120">
          </el-table-column>
          <el-table-column
            prop="node"
            :label="$t('lang.index_table_title7')"
            width="150">
          </el-table-column>
        </el-table>
      </div>

          <div class="block_page">
            <pageSize :page-data="pageData" :layout="`prev, pager, next,jumper`" @currentChange="currentChange"
                      @sizeChange="sizeChange"/>
          </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '@/components/MHeader'
import Footer from '@/components/MFooter'
import pageSize from '@/components/MPage'
import dateFns from 'date-fns'

export default {
    components: {
        Header,
        Footer,
        pageSize
    },
    data() {
        return {
             pageData: {
                currentPage: 1,
                pageSize: 10,
                total: 0
            },
            blockData: [],
            tableData:[]
        }
    },
    computed: {},
    mounted() {
            this.getBlockList(this.pageData.currentPage)
            this.getBlockSummary()
        },
        methods: {
            sizeChange(size) {
                this.pageData.currentPage = 1;
                this.pageData.pageSize = size
            },
            currentChange(val) {
                this.pageData.currentPage = val;
                this.getBlockList(val)
                this.getBlockSummary()
            },
            getBlockList(p){
                this.$http.get("api/block/recent", {p: p}).then(res => {
                    if (res.data.code === 200){
                        this.tableData = res.data.data.block
                        this.pageData.currentPage = res.data.data.page.current_page
                        this.pageData.total = res.data.data.page.total
                        this.pageData.pageSize = res.data.data.page.page_size
                    }
                })
            },
            getBlockSummary(){
                this.$http.get("api/block/summary").then(res => {
                    if (res.data.code === 200){
                        this.blockData = res.data.data
                    }
                })
            },
            filterDate(value, type) {
                if (!value) return ''
                if(value > 0){
                    value = value * 1000
                }
                const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
                return dateFns.format(value, filterType)
            }
        }
}


</script>


<style scoped>
    .m-content{
        padding: 1rem;
        background: #E6E9EE;
    }
    .m-head{
        background: #ffffff;
    }
    .m-head .m-item{
        text-align: center;
        border-bottom: 1px solid #F0F0F0;
        padding: 1rem 0;
    }
    .m-item p{
        color: #888888;
        font-size: 12px;
    }
    .item-lebel{
        font-size: 17px;
        margin-bottom: .5rem;
    }
    .m-tips{
        padding: .7rem;
        text-align: left;
    }
</style>



