<template>
  <div>
    <Header/>
    <el-container class="main-container">
      <el-main>

        <div class="con-content">
          <div class="con-con" style="margin-bottom: 20px;">
            <div class="con-total">
              <el-row>
                <el-col><label for="">{{ blockData.supply }} GKC</label>
                  <p>{{ $t('lang.index_title_supply') }}</p></el-col>
                <el-col><label for="">{{ blockData.height }}</label>
                  <p>{{ $t('lang.index_title_height') }}</p></el-col>
                <el-col>
                  <label for="">{{ blockData.masternodes }}</label>
                  <p>{{ $t('lang.index_title_dpos') }}</p>
                </el-col>
                <el-col>
                  <label for="">{{ blockData.node }}</label>
                  <p>{{ $t('lang.index_title_masternodes') }}</p>
                </el-col>
              </el-row>
            </div>
          </div>
        </div>

        <div class="con-content">
          <div class="con-title">
            {{ $t('lang.index_detail_title') }}
          </div>
          <el-table
            :data="tableData"
            style="">
            <el-table-column
              prop="block"
              :label="$t('lang.index_table_title1')"
              width="100">
              <template slot-scope="scope">
                <router-link tag="a" :to="{path:'/block',query:{height:scope.row.block}}">{{ scope.row.block }}
                </router-link>
              </template>
            </el-table-column>
            <el-table-column
              prop="approx_time"
              :label="$t('lang.index_table_title2')"
              width="190">
              <template slot-scope="scope">
                {{ filterDate(scope.row.approx_time) }}
              </template>
            </el-table-column>
            <el-table-column
              prop="transactions"
              :label="$t('lang.index_table_title3')"
              align="center">
            </el-table-column>
            <el-table-column
              prop="value_out"
              :label="$t('lang.index_table_title4')">
            </el-table-column>
            <el-table-column
              prop="difficulty"
              :label="$t('lang.index_table_title5')">
            </el-table-column>
            <el-table-column
              prop="average_age"
              :label="$t('lang.index_table_title6')">
            </el-table-column>
            <el-table-column
              prop="node"
              :label="$t('lang.index_table_title7')"
              width="220">
            </el-table-column>
<!--            <el-table-column-->
<!--              prop="chain_age"-->
<!--              label="Chain Age"-->
<!--            >-->
<!--            </el-table-column>-->
          </el-table>
          <div class="block_page">
            <pageSize :page-data="pageData" :layout="`prev, pager, next,jumper`" @currentChange="currentChange"
                      @sizeChange="sizeChange"/>
          </div>
        </div>

      </el-main>
    </el-container>
    <Footer/>
  </div>
</template>

<script>
    import Header from '@/components/Header'
    import Footer from '@/components/Footer'
    import pageSize from '@/components/Page'
    import dateFns from 'date-fns'

    export default {
        components: {
            Header,
            Footer,
            pageSize
        },
        data() {
            return {
                tableData: [],
                blockData: [],
                pageData: {
                    currentPage: 1,
                    pageSize: 10,
                    total: 0
                }
            }
        },
        computed: {},
        mounted() {
            this.getBlockList(this.pageData.currentPage)
            this.getBlockSummary()
        },
        methods: {
            sizeChange(size) {
                this.pageData.currentPage = 1;
                this.pageData.pageSize = size
            },
            currentChange(val) {
                this.pageData.currentPage = val;
                this.getBlockList(val)
                this.getBlockSummary()
            },
            getBlockList(p){
                this.$http.get("api/block/recent", {p: p}).then(res => {
                    if (res.data.code === 200){
                        this.tableData = res.data.data.block
                        this.pageData.currentPage = res.data.data.page.current_page
                        this.pageData.total = res.data.data.page.total
                        this.pageData.pageSize = res.data.data.page.page_size
                    }
                })
            },
            getBlockSummary(){
                this.$http.get("api/block/summary").then(res => {
                    if (res.data.code === 200){
                        this.blockData = res.data.data
                    }
                })
            },
            filterDate(value, type) {
                if (!value) return ''
                if(value > 0){
                    value = value * 1000
                }
                const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
                return dateFns.format(value, filterType)
            }
        }
    }


</script>
<style>
  .el-main {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: center;
  }
  .el-main .con-content {
    width: 1200px;
    min-width: 1200px;
  }

  .el-main .con-content .con-con {
    background: rgba(255, 255, 255, 1);
    border-radius: 4px;
  }

  .el-main .con-content .con-con .con-total {
    padding: 0 50px;
  }

  .el-main .con-content .con-con > .con-total .el-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid rgba(240, 240, 240, 1);
  }

  .el-main .con-content .con-con > .con-total .el-row .el-col {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 25px 0;
    height: 70px;
    border-left: 4px solid rgba(240, 240, 240, 1);
  }

  .el-main .con-content .con-con > .con-total .el-row .el-col:first-child {
    border-left: none;
  }
  .el-main .con-content .con-con > .con-total .el-row .el-col label {
    font-size: 20px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
    height: 35px;
    line-height: 35px;
  }
  .el-table__body-wrapper {
    overflow: visible;
  }
  .el-main .con-content .con-con > .con-total .el-row .el-col p {
    font-size: 14px;
    font-weight: 200;
    color: rgba(136, 136, 136, 1);
    height: 35px;
    line-height: 35px;
  }

  .el-main .con-content .con-con .el-tabs .el-tabs__header {
    display: flex;
    justify-content: center;
    margin-top: 0;
    margin-bottom: 30px;
    border: none;
  }

  .el-main .con-content .con-con .el-tabs .el-tabs__nav {
    border: none;
  }

  .el-main .con-content .con-con .el-tabs .el-tabs__item {
    width: 144px;
    height: 40px;
    background: rgba(0, 93, 166, 1);
    border-radius: 4px;
    font-size: 14px;
    font-weight: 400;
    color: rgba(255, 255, 255, 1);
  }

  .el-main .con-content .con-con .el-tabs .el-row {
    display: flex;
    flex-direction: column;
    justify-content: left;
    background: rgba(255, 255, 255, 1);
    border-radius: 4px;
    font-size: 16px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
    align-items: center;
  }

  .el-main .con-content .con-con .el-tabs .el-row .el-col {
    display: flex;
    flex-direction: row;
    justify-content: left;
    height: 60px;
    border-bottom: 1px solid rgba(245, 245, 245, 1);
    align-items: center;
    padding-left: 10px;
  }

  .el-main .con-content .con-con .el-tabs .el-row label {
    width: 25%;
    text-align: left;
  }

  .el-main .con-content .con-title {
    height:60px;
    background:rgba(243,244,247,1);
    border-radius:4px 4px 0 0;
    font-size:16px;
    font-weight:400;
    color:rgba(0,0,0,1);
    line-height:18px;
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 0 27px;
  }
  .el-main .con-content .el-table {
    padding: 0 27px;
  }
  .el-main .con-content .el-table tr {
    height: 60px;
    padding: 0 27px;
    width: 1146px;
  }
  .el-main .con-content .el-table thead .cell {
    font-size:16px;
    font-weight:400;
    color:rgba(0,0,0,1);
    padding: 0;
  }
  .el-main .con-content .el-table tbody .cell {
    font-size:14px;
    font-weight:400;
    color:rgba(0,0,0,1);
    padding: 0;
  }
</style>




