import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/home/index'
import Block from '@/views/home/block'
import Address from '@/views/home/address'
import MIndex from '@/views/mobile/index'
import Tx from '@/views/home/tx'
import Nodes from '@/views/home/node'
import Agent from '@/views/home/agent'
import List from '@/views/home/list'

Vue.use(Router);

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      meta:{type:'pc',link:'/mobile'},
      component: Index
    },{
      path: '/block',
      name: 'Block',
      meta:{type:'pc',link:'mblock'},
      component: Block
    },{
      path: '/address',
      name: 'Address',
      meta:{type:'pc',link:'maddress'},
      component: Address
    },{
      path: '/tx',
      name: 'Tx',
      meta:{type:'pc',link:'mtx'},
      component: Tx
    },{
      path: '/node',
      meta:{type:'pc',link:'mnode'},
      name: 'Nodes',
      component: Nodes
    },{
      path: '/agent',
      meta:{type:'pc',link:'magent'},
      name: 'Agent',
      component: Agent
    },{
      path: '/list',
      meta:{type:'pc',link:'magentList'},
      name: 'List',
      component: List
    },{
      path: '/mobile',
      meta:{type:'web',link:'/'},
      name: 'Mobile',
      component: () => import('@/views/mobile/index')
    },{
      path: '/mblock',
      meta:{type:'web',link:'block'},
      name: 'mblock',
      component: () => import('@/views/mobile/block')
    },{
      path: '/magent',
      meta:{type:'web',link:'agent'},
      name: 'magent',
      component: () => import('@/views/mobile/pos')
    },{
      path: '/magentList',
      meta:{type:'web',link:'list'},
      name: 'magentList',
      component: () => import('@/views/mobile/posDetails')
    },{
      path: '/maddress',
      meta:{type:'web',link:'address'},
      name: 'maddress',
      component: () => import('@/views/mobile/address')
    },{
      path: '/mtx',
      meta:{type:'web',link:'tx'},
      name: 'mtx',
      component: () => import('@/views/mobile/tx')
    },{
      path: '/mnode',
      meta:{type:'web',link:'node'},
      name: 'mnode',
      component: () => import('@/views/mobile/node')
    },{
      path: '/mtoken',
      meta:{type:'web',link:'token'},
      name: 'mtoken',
      component: () => import('@/views/mobile/token')
    },{
      path: '/token',
      meta:{type:'pc',link:'mtoken'},
      name: 'Token',
      component: () => import('@/views/home/token')
    },{
      path: '/tokendetail',
      meta:{type:'pc',link:'mtokendetail'},
      name: 'Tokendetail',
      component: () => import('@/views/home/tokendetail')
    },{
      path: '/tokenone',
      meta:{type:'pc',link:'mtokenone'},
      name: 'Tokenone',
      component: () => import('@/views/home/tokenone')
    },{
      path: '/search',
      meta:{type:'web',link:''},
      name: 'Search',
      component: () => import('@/views/mobile/search')
    },{
      path: '/searchone',
      meta:{type:'web',link:''},
      name: 'Searchone',
      component: () => import('@/views/mobile/searchone')
    },{
      path: '/mtokenone',
      meta:{type:'web',link:'tokenone'},
      name: 'Mtokenone',
      component: () => import('@/views/mobile/tokenone')
    },{
      path: '/mtokendetail',
      meta:{type:'web',link:'tokendetail'},
      name: 'Mtokendetail',
      component: () => import('@/views/mobile/tokendetail')
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    }
  },
  history: true
});

router.beforeEach((to, from, next) => {
  const flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
  if(flag){
    if(to.meta.type === 'web'){
      next()
    }else{
      router.replace({path:to.meta.link,query:{...to.query}})
      next()
    }
  } else {
    if(to.meta.type === 'pc'){
      next()
    }else{
      router.replace({path:to.meta.link,query:{...to.query}})
      next()
    }
  }
  // next();
});
export default router;

