<template>
  <div>
    <Header/>
    <el-container class="main-container">
      <el-main>
        <div class="node-content">
          <div class="con-title">
            {{ $t('lang.masternode_title')}}
          </div>
          <el-table
            :data="tableData"
            style="width: 100%;">
            <el-table-column
              prop="address"
              :label="$t('lang.masternode_table1')"
              width="350">
            </el-table-column>
            <el-table-column
              prop="status"
              :label="$t('lang.masternode_table2')">
               <template slot-scope="scope">
                   <span style="color: #000000;" v-if="scope.row.status === 'ENABLED'">{{ scope.row.status }}</span>
                   <span style="color: #888888;" v-else>{{ scope.row.status }}</span>
               </template>
            </el-table-column>
            <el-table-column
              prop="version"
              :label="$t('lang.masternode_table3')">
            </el-table-column>
            <el-table-column
              prop="lasttime"
              :label="$t('lang.masternode_table4')"
              width="250">
              <template slot-scope="scope">
                {{ filterDate(scope.row.lasttime) }}
              </template>
            </el-table-column>
            <el-table-column
              prop="lasttime"
              :label="$t('lang.masternode_table5')"
              width="250">
              <template slot-scope="scope">
                {{ filterDate(scope.row.lastpaid) }}
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-main>
    </el-container>
    <Footer/>
  </div>
</template>

<script>
    import Header from '@/components/Header'
    import Footer from '@/components/Footer'
    import pageSize from '@/components/Page'
    import dateFns from 'date-fns'

    export default {
        components: {
            Header,
            Footer,
            pageSize
        },
        data() {
            return {
                tableData: []
            }
        },
        computed: {},
        mounted() {
            this.getNodeList()
        },
        methods: {
            getNodeList(){
                this.$http.get("api/node/list").then(res => {
                    if (res.data.code === 200){
                        this.tableData = res.data.data
                    }
                })
            },
            filterDate(value, type) {
                if (!value) return ''
                if(value > 0){
                    value = value * 1000
                }
                const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
                return dateFns.format(value, filterType)
            }
        }
    }


</script>
<style>
  .el-main {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: center;
  }
  .el-main .node-content {
    width: 1200px !important;
    min-width: 1200px;
  }
  .el-main .node-content .con-title {
    height:60px;
    background:rgba(243,244,247,1);
    border-radius:4px 4px 0 0;
    font-size:18px;
    font-weight:400;
    color:rgba(0,0,0,1);
    line-height:18px;
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 0 27px;
  }
  .el-main .node-content .el-table {
    padding: 0 27px;
  }
  .el-main .node-content .el-table tr {
    height: 60px;
  }
  .el-main .node-content .el-table thead .cell {
    font-size:16px;
    font-weight:400;
    color:rgba(0,0,0,1);
    padding-right: 0;
  }
  .el-main .node-content .el-table tbody .cell {
    font-size:14px;
    font-weight:400;
    color:rgba(0,0,0,1);
    padding-right: 0;
  }
</style>




