<template>
  <div>
    <Header/>
    <el-container class="main-container">
      <el-main>
        <div class="tx-content">
          <div class="con-header">
            <el-row type="flex">
              <el-col><img src="@/assets/img/exchange2.png" alt="">{{ $t('lang.trans_title') }}</el-col>
              <el-col>{{ txid }}</el-col>
            </el-row>
          </div>
        </div>

        <div class="tx-content">
          <div class="con-con">
            <div class="con-total">
              <el-row>
                <el-col><label for="">{{ transData.input }} GKC</label>
                  <p>{{ $t('lang.trans_detail1') }}</p></el-col>
                <el-col><label for="">{{ transData.output }} GKC</label>
                  <p>{{ $t('lang.trans_detail2') }}</p></el-col>
                <el-col><label for="">{{ transData.fees }} GKC</label>
                  <p>{{ $t('lang.trans_detail3') }}</p></el-col>
              </el-row>
            </div>
            <el-tabs>
              <!--              <button @click="handleHidden">Advanced Details</button>-->
              <div class="tx-content">
                <el-row>
                  <el-col><label for="">{{ $t('lang.trans_detail4') }}</label>
                    <p>{{ transData.size }} </p></el-col>
                  <el-col><label for="">{{ $t('lang.trans_detail5') }}</label>
                    <p>{{ transData.height }}</p></el-col>
                  <el-col><label for="">{{ $t('lang.trans_detail6') }}</label>
                    <p>{{ filterDate(transData.timestamp) }}</p></el-col>
                  <el-col v-if="raw.type === 'Call' || raw.type === 'Create'"><label for="">{{ $t('lang.trans_detail7') }}</label>
                    <p>
                      <router-link tag="a" :to="{path:'/tokendetail',query:{txid: transData.hash }}">{{
                        $t('lang.trans_detail8') }}
                      </router-link>
                    </p>
                  </el-col>
                </el-row>
              </div>
            </el-tabs>
          </div>
        </div>

        <div class="tx-content">
          <div class="con-title">
            {{ $t('lang.trans_trans_title') }}
          </div>
          <div class="con-log">
            <div class="con-log-title">
              <img src="@/assets/img/exchange.png" alt="">
              <p>
                <router-link tag="a" :to="{path:'/tx',query:{txid: transData.hash }}">{{ transData.hash }}
                </router-link>
              </p>
            </div>
            <div class="con-log-con">
              <div class="con-log-left">
                <div class="con-log-con-title">
                  {{ $t('lang.trans_trans1') }}
                </div>
                <el-row v-for="(item, index) in transInput"
                        :key="index"
                        :prop="item.prop">
                  <el-col><p>{{ item.amount }} GKC</p> From</el-col>
                  <el-col>
                    <!--                    <div :id="item.address+index" class="qrdata"></div>-->
                    <img src="@/assets/img/qr.png" alt="">
                    <p v-if="isGoto(item.address)" style="display: inline-block;vertical-align: middle;" v-bind:title="item.address">
                      <router-link tag="a" :to="{path:'/address',query:{address: item.address }}">{{ item.address }}
                      </router-link>
                    </p>
                    <p v-else style="display: inline-block;vertical-align: middle;" v-bind:title="item.address">
                      <a href="javascript:void(0)">{{ item.address }}
                      </a>
                    </p>
                  </el-col>
                </el-row>
              </div>
              <div class="con-log-center">
                <img src="@/assets/img/right.png" alt="">
              </div>
              <div class="con-log-right">
                <div class="con-log-con-title">
                  {{ $t('lang.trans_trans2') }}
                </div>
                <el-row v-for="(item, index) in transOutput"
                        :key="index"
                        :prop="item.prop">
                  <el-col>
                    <div v-if="item.address === 'unknown'">
                      <p>Empty</p>
                    </div>
                    <div
                      v-else-if="item.address === '0x20 0xc87b1c605f7e14a848ff5c2cb4805446ce4c3bf71da1829dffd05117a977f09b 0x20 0x21b463e3b52f6201c0ad6c991be0485b6ef8c092e64583ffa655cc1b171fe856 0xc'">
                      <p>contract</p>
                    </div>
                    <div v-else>
                      <p style="display: inline-block">{{ item.amount }} GKC</p> To
                    </div>
                  </el-col>
                  <el-col>

                    <div v-if="item.address === 'unknown'">
                      <img src="@/assets/img/unkown.png" alt="">
                      <p style="display: inline-block">Coinbase</p>
                    </div>
                    <div
                      v-else-if="item.address === '0x20 0xc87b1c605f7e14a848ff5c2cb4805446ce4c3bf71da1829dffd05117a977f09b 0x20 0x21b463e3b52f6201c0ad6c991be0485b6ef8c092e64583ffa655cc1b171fe856 0xc'">
                      <img src="@/assets/img/unkown.png" alt="">
                      <p style="display: inline-block">Non-standard script</p>
                    </div>
                    <div v-else>
                      <!--                      <div :id="item.address+index" class="qrdata"></div>-->
                      <img src="@/assets/img/qr.png" alt="">
                      <p v-if="isGoto(item.address)" style="display: inline-block;vertical-align: middle;" v-bind:title="item.address">
                        <router-link tag="a" :to="{path:'/address',query:{address: item.address }}">{{ subStr(item.address) }}
                        </router-link>
                      </p>
                      <p v-else style="display: inline-block;vertical-align: middle;" v-bind:title="item.address">
                        <a href="javascript:void(0)">{{ subStr(item.address) }}
                        </a>
                      </p>
                    </div>
                  </el-col>
                </el-row>
              </div>
            </div>
            <div class="con-log-bottom" v-if="transData.total_out !== ''">
              {{ $t('lang.trans_trans3') }}： <p>{{ transData.total_out }} GKC</p>
            </div>
          </div>
        </div>
      </el-main>
    </el-container>
    <Footer/>
  </div>
</template>

<script>
  import Header from '@/components/Header'
  import Footer from '@/components/Footer'
  import pageSize from '@/components/Page'
  import QRCode from 'qrcodejs2'
  import dateFns from 'date-fns'

  export default {
    components: {
      Header,
      Footer,
      pageSize
    },
    data() {
      return {
        transInput: [],
        transOutput: [],
        transType: 'first',
        transData: [],
        txid: '',
        txinfo: false,
        raw: []
      }
    },
    created() {
      this.txid = this.$route.query.txid;
    },
    watch: {
      $route() {
        this.txid = this.$route.query.txid;
        this.getTransInfo(this.txid)
      }
    },
    computed: {},
    mounted() {
      this.getTransInfo(this.txid)
    },
    methods: {
      getTransInfo(txid) {
        this.$http.post("api/block/trans", {txid: txid}).then(res => {
          if (res.data.code === 200) {
            this.transData = res.data.data.trans
            if (res.data.data.input) {
              this.transInput = []
              for (let index in res.data.data.input) {
                res.data.data.input[index].sort = parseInt(index) + 1
                this.transInput.push(res.data.data.input[index])
              }
            }
            if (res.data.data.output) {
              this.transOutput = []
              for (let index in res.data.data.output) {
                res.data.data.output[index].sort = parseInt(index) + 1
                this.transOutput.push(res.data.data.output[index])
              }
            }
            this.raw = res.data.data.raw
            setTimeout(() => {
              $(".qrdata").html("")
              // for (const index in this.transInput ) {
              //     this.qrcode(20, 20, this.transInput[index].address + index, this.transInput[index].address)
              // }
              // for (const index2 in this.transOutput ) {
              //     this.qrcode(20, 20, this.transOutput[index2].address + index2, this.transOutput[index2].address)
              // }
            }, 500);
          }
        })
      },
      handleHidden() {
        if (!this.txinfo) {
          this.txinfo = true
        } else {
          this.txinfo = false
        }
      },
      qrcode(width, height, id, address) {
        new QRCode(id, {
          text: address,
          width: width,
          height: height,
          colorDark: '#333333',
          colorLight: '#ffffff',
          correctLevel: QRCode.CorrectLevel.L
        })
      },
      filterDate(value, type) {
        if (!value) return ''
        if (value > 0) {
          value = value * 1000
        }
        const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
        return dateFns.format(value, filterType)
      },
      subStr(str) {
        if (str === "") {
          return ""
        }
        if (str.length === 34) {
          return str
        }
        if (str === "Non-standard script"){
          return str
        }
        str = str.substr(0, 50);
        str += "...";
        return str
      },
      isGoto(str) {
        if (str.indexOf("G") === 0 && str.length === 34){
          return true;
        }else{
          return false;
        }
      }
    }
  }


</script>
<style>
