    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: center;
  }

  .el-main .tx-content {
    width: 1200px !important;
    min-width: 1200px;
  }

  .el-main .tx-content .con-header {
    height: 130px;
    background-image: url(../../assets/img/trade_bg.png);
    background-size: 100% 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
  }

  .el-main .tx-content .con-header img {
    width: 28px;
    height: 28px;
    margin-right: 10px;
  }

  .el-main .tx-content .con-header .el-row {
    width: 100%;
    height: 130px;
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: center;
  }

  .el-main .tx-content .con-header .el-row .el-col {
    width: 100%;
    display: flex;
    align-items: center;
    font-size: 24px;
    font-weight: 400;
    color: rgba(255, 255, 255, 1);
    padding-left: 18px;
  }

  .el-main .tx-content .con-header .el-row .el-col:last-child {
    font-size: 18px;
    margin-top: 26px;
  }

  .el-main .tx-content .con-title {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
  }

  .el-main .tx-content .con-con {
    background: rgba(255, 255, 255, 1);
    border-radius: 4px;
    margin-top: 40px;
  }

  .el-main .tx-content .con-con .con-total {
    padding: 0 50px;
  }

  .el-main .tx-content .con-con > .con-total .el-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid rgba(240, 240, 240, 1);
  }

  .el-main .tx-content .con-con > .con-total .el-row .el-col {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 25px 0;
    height: 70px;
    border-left: 4px solid rgba(240, 240, 240, 1);
  }

  .el-main .tx-content .con-con > .con-total .el-row .el-col:first-child {
    border-left: none;
  }

  .el-main .tx-content .con-con > .con-total .el-row .el-col label {
    font-size: 20px;
    color: rgba(0, 0, 0, 1);
    height: 35px;
    line-height: 35px;
    font-weight: 400;
  }

  .el-main .tx-content .con-con > .con-total .el-row .el-col p {
    font-size: 14px;
    font-weight: 200;
    color: rgba(136, 136, 136, 1);
    height: 35px;
    line-height: 35px;
  }

  .el-main .tx-content .con-con .el-tabs .el-tabs__header {
    display: flex;
    justify-content: center;
    margin-top: 0;
    margin-bottom: 30px;
    border: none;
  }

  .el-main .tx-content .con-con .el-tabs .el-tabs__nav {
    border: none;
  }

  .el-main .tx-content .con-con .el-tabs button {
    width: 144px;
    height: 40px;
    background: rgba(0, 93, 166, 1);
    border-radius: 4px;
    font-size: 14px;
    font-weight: 400;
    color: rgba(255, 255, 255, 1);
    outline: none;
    border: none;
    margin-bottom: 30px;
  }

  .el-main .tx-content .con-con .el-tabs .el-row {
    display: flex;
    flex-direction: column;
    justify-content: left;
    background: rgba(255, 255, 255, 1);
    border-radius: 4px;
    font-size: 16px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
    align-items: center;
  }

  .el-main .tx-content .con-con .el-tabs .el-row .el-col {
    display: flex;
    flex-direction: row;
    justify-content: left;
    height: 60px;
    border-bottom: 1px solid rgba(245, 245, 245, 1);
    align-items: center;
    padding-left: 27px;
  }

  .el-main .tx-content .con-con .el-tabs .el-row label {
    width: 25%;
    text-align: left;
  }

  .el-main .tx-content .con-title {
    font-size: 20px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
  }

  .el-main .tx-content .con-log {
    background: rgba(255, 255, 255, 1);
    border-radius: 4px;
    margin-top: 24px;
    padding-top: 40px;
    padding-bottom: 32px;
  }

  .el-main .tx-content .con-log .con-log-title {
    display: flex;
    justify-content: center;
    font-size: 18px;
    font-weight: 400;
    color: rgba(0, 93, 166, 1);
  }

  .el-main .tx-content .con-log .con-log-title img {
    width: 28px;
    height: 28px;
    margin-right: 10px;
  }

  .el-main .tx-content .con-log .con-log-title p {
    height: 28px;
    line-height: 28px;
  }

  .el-main .tx-content .con-log .con-log-con {
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    padding-left: 30px;
  }

  .el-main .tx-content .con-log .con-log-con .con-log-left {
    width: 42%;
  }

  .el-main .tx-content .con-log .con-log-con .con-log-con-title {
    font-size: 18px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
  }

  .el-main .tx-content .con-log .con-log-con .con-log-center {
    width: 16%;
    display: flex;
    justify-content: center;
    margin-top: 60px;
  }

  .el-main .tx-content .con-log .con-log-con .con-log-center img {
    width: 68px;
    height: 68px;
  }

  .el-main .tx-content .con-log .con-log-con .con-log-right {
    width: 42%;
  }

  .el-main .tx-content .con-log .con-log-con .el-row {
    width: 475px;
    height: 100px;
    border: 2px solid rgba(230, 230, 230, 1);
    border-radius: 10px;
    margin-top: 15px;
    font-size: 18px;
    font-weight: 400;
    color: #888888;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
  }

  .el-main .tx-content .con-log .con-log-con .el-row .el-col {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    height: 20px;
    line-height: 20px;
    padding-left: 20px;
  }

  .el-main .tx-content .con-log .con-log-con .el-row .el-col img {
    width: 20px;
    height: 20px;
    margin-right: 8px;
    vertical-align: middle;
  }

  .el-main .tx-content .con-log .con-log-con .el-row .el-col:first-child p {
    color: rgba(0, 0, 0, 1);
    margin-right: 10px;
  }

  .el-main .tx-content .con-log .con-log-con .el-row .el-col:last-child {
    margin-top: 15px;
  }

  .el-main .tx-content .con-log .con-log-con .el-row .el-col:last-child p {
