<script>
  import Header from '@/components/Header'
  import Footer from '@/components/Footer'
  import QRCode from 'qrcodejs2'
  import pageSize from '@/components/Page'
  import dateFns from 'date-fns'

  export default {
    components: {
      Header,
      Footer,
      pageSize
    },
    data() {
      return {
        addressData: [],
        transData: [],
        address: '',
        userData: [],
        pageData: {
          currentPage: 1,
          pageSize: 10,
          total: 0
        },
        token: ""
      }
    },
    created() {
      this.address = this.$route.query.address;
    },
    computed: {},
    watch: {
      $route() {
        this.address = this.$route.query.address;
        this.getBalanceByAddress(this.address)
        this.getBlockInfoByAddress(this.address)
        this.getUserData(this.address)
      }
    },
    mounted() {
      this.getBalanceByAddress(this.address)
      this.getUserData(this.address)
      this.getBlockInfoByAddress(this.address)
    },
    methods: {
      sizeChange(size) {
        this.pageData.currentPage = 1;
        this.pageData.pageSize = size
      },
      currentChange(val) {
        this.pageData.currentPage = val;
        this.getBlockInfoByAddress(this.address)
      },
      getBalanceByAddress(address) {
        this.$http.post("api/block/balance", {address: address}).then(res => {
          if (res.data.code === 200) {
            this.addressData = res.data.data.address
            setTimeout(() => {
              $(".qrdata").html("")
              this.qrcode(70, 70, this.addressData.address + 'button', this.addressData.address)
            }, 500);

          }
        })
      },
      getBlockInfoByAddress(address) {
        this.$http.post("api/block/address", {address: address, p: this.pageData.currentPage}).then(res => {
          if (res.data.code === 200) {
            this.transData = res.data.data.list
            this.pageData.currentPage = res.data.data.page.current_page
            this.pageData.total = res.data.data.page.total
            this.pageData.pageSize = res.data.data.page.page_size
            this.token = res.data.data.token
          }
        })
      },
      qrcode(width, height, id, address) {
        new QRCode(id, {
          text: address,
          width: width,
          height: height,
          colorDark: '#333333',
          colorLight: '#ffffff',
          correctLevel: QRCode.CorrectLevel.L
        })
      },
      getUserData(address) {
        this.$http.post("api/node/user", {address: address}).then(res => {
          if (res.data.code === 200) {
            this.userData = res.data.data
          }
        })
      },
      filterDate(value, type) {
        if (!value) return ''
        if (value > 0) {
          value = value * 1000
        }
        const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
        return dateFns.format(value, filterType)
      },
      subStr(str){
        if (str === "") {
          return ""
        }
        if (str.length === 34) {
          return str
        }
        str = str.substr(0, 50);
        str += "...";
        return str
      },
      isGoto(str) {
        if (str.indexOf("G") === 0 && str.length === 34){
          return true;
        }else{
          return false;
        }
      }
    }
  }


</script>
