<template>
  <div class="m-header">
    <div class="m-logo">
      <router-link tag="a" :to="{path:'/mobile'}">
        <img src="@/assets/img/logo.png" alt="">
      </router-link>
      <div class="" @click="handelNav">
        <el-dropdown trigger="click" @command="handleCommand" @visible-change="handleVisible">
                    <span class="el-dropdown-link">
                        <img v-if="!menu" src="@/assets/m/menuOpen.png" alt="" srcset="">
                        <img v-else src="@/assets/m/menuClose.png" alt="" srcset="">
                    </span>
          <el-dropdown-menu slot="dropdown" class="nav-dropdown">
            <el-dropdown-item>
              <router-link tag="a" to="/mobile">
                <p>{{ $t('lang.header_index')}}</p>
              </router-link>
              <router-link tag="a" to="/magent">
                <p>{{ $t('lang.header_dpos')}}</p>
              </router-link>
              <router-link tag="a" to="/mnode">
                <p>{{ $t('lang.header_masternodes')}}</p>
              </router-link>
              <router-link tag="a" to="/mtoken">
                <p>{{ $t('lang.header_Token')}}</p>
              </router-link>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <div class="lang" ref="userInfo">
      <div class="default" @click="enter()">
        <img :src="langimg" alt="" class="lang_log">
        <span>{{ value }}</span>
        <img src="@/assets/img/lang_select.png" alt="">
      </div>
      <div class="lang_list" v-show="lang" @click="leave()">
        <ul>
          <li v-for="item in langs" @click="langchange(item.icon)">{{ item.name }}</li>
        </ul>
      </div>
    </div>
    <div class="m-search">
      <el-input v-model="input" :placeholder="$t('lang.header_search')" @keydown.enter.native="getBlockSearch">
        <div slot="suffix" class="search-btn" @click="getBlockSearch">
          <img src="@/assets/img/search.png" alt="">
        </div>
      </el-input>
    </div>

  </div>
</template>

<script>
  import Cookies from "js-cookie";

  export default {
    data() {
      return {
        input: '',
        menu: false,
        langimg: Cookies.get("lang_img") || "./static/img/en.png",
        value: Cookies.get("lang_name") || "English",
        langs: {
          en: {
            name: "English",
            icon: "en"
          }
          ,
          zh: {
            name: "简体中文",
            icon: "zh"
          }
        },
        lang: false
      }
    },
    mounted() {
      let _this = this;
      document.addEventListener('click', function (e) {
        if (!!_this.$refs.userInfo.contains(e.target)) {
          return;
        }
        _this.lang = false
      })
    },
    methods: {
      getBlockSearch() {
        let search = this.input
        if (!search) {
          this.$message.error(this.$t('lang.header_msg1'));
          return
        }
        this.$http.post("api/block/search", {search: search}).then(res => {
          if (res.data.code === 200) {
            this.type = res.data.data.type
            if (this.type === "block") {
              let height = parseInt(res.data.data.search)
              this.$router.push({path: '/mblock', query: {height: height}});
            } else if (this.type === "transaction") {
              let txid = res.data.data.search
              this.$router.push({path: '/mtx', query: {txid: txid}});
            } else if (this.type === "address") {
              let address = res.data.data.search
              this.$router.push({path: '/maddress', query: {address: address}});
            } else {
              this.$message.error(this.$t('lang.header_msg2'));
            }
          } else {
            this.$message.error(this.$t('lang.header_msg2'));
          }
        })
      },
      handelNav() {
        this.menu = !this.menu
      },
      handleCommand(command) {
        this.menu = !this.menu
      },
      handleVisible(val) {
        if (!val) {
          this.menu = false
        }
      },
      leave() {
        this.lang = false
      },
      enter() {
        this.lang = true
      },
      langchange(lang) {
        for (let index in this.langs) {
          if (index === lang) {
            this.value = this.langs[index].name
            Cookies.set("lang", lang)
            Cookies.set("lang_name", this.value)
            Cookies.set("lang_img", "./static/img/" + lang + ".png")
            $('.lang_log').attr("src", "./static/img/" + lang + ".png")
            this.$i18n.locale = lang
          }
        }
        this.lang = false
      }
    }
  }
</script>
<style scoped>
  .lang {
    position: absolute;
    right: 50px;
    top: 1rem;
    display: flex;
    align-items: center;
    height: 80px;
    flex-direction: column;
  }

  .lang .default {
    height: 30px;
    cursor: pointer;
    display: flex;
    align-items: center;
  }

  .lang .default span {
    font-size: 14px;
    color: #ffffff;
    vertical-align: middle;
    display: block;
    height: 30px;
    line-height: 30px;
    width: 60px;
    text-align: left;
  }

  .lang .default img {
    width: 30px;
    height: 30px;
    vertical-align: middle;
  }

  .lang .default img:first-child {
    margin-right: 10px;
  }

  .lang_list {
    background: rgba(255, 255, 255, 1);
    opacity: 1;
    border-radius: 2px;
    width: 100px;
    position: absolute;
    top: 30px;
    right: 0;
    z-index: 10000;
  }

  .lang_list ul {
    padding: 5px 10px;
    font-size: 14px;
    font-weight: 400;
    color: rgba(32, 38, 64, 1);
    opacity: 1;
  }

  .lang_list ul li {
    line-height: 24px;
    text-align: left;
    cursor: pointer;
  }

  .lang_list ul:hover {
    display: block;
  }

  .lang_list ul li:hover {
    color: rgba(0, 93, 166, 1);
  }

  .m-header {
    background-color: rgba(0, 93, 166, 1);
    text-align: left;
    padding: 1rem;
  }

  .m-header .m-logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .m-header .m-logo img {
    height: 2rem;
  }

  .m-search {
    margin-top: .8rem;
  }
</style>



