</script>
<style>
  .el-footer {
    height: 100px !important;
    line-height: 100px;
    background:rgba(5,39,72,1);
    display: flex;
    justify-content: center;
  }
  .el-footer .el-row {
    width: 1200px;
    min-width: 1200px;
  }
  .el-footer .el-row .el-col {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
