<template>
  <div>
    <Header/>
    <el-container class="main-container">
      <el-main>
        <div class="agent-content">
          <div class="con-con">
            <div class="con-total">
              <el-row>
                <el-col><label for="">{{ nodeData.total_node}}</label>
                  <p>{{ $t('lang.dpos_title1') }}</p></el-col>
                <el-col><label for="">{{ nodeData.total_user}}</label>
                  <p>{{ $t('lang.dpos_title2') }}</p></el-col>
                <el-col><label for="">{{ nodeData.total_amount}}</label>
                  <p>{{ $t('lang.dpos_title3') }}</p></el-col>
                <el-col><label for="">{{ nodeData.total_divident}}</label>
                  <p>{{ $t('lang.dpos_title4') }}</p></el-col>
              </el-row>
            </div>
          </div>
        </div>

        <div class="agent-content">
          <div class="con-title">
            <el-button @click="showNodeOrReward('node')" :class="{navtitle: nodePage}">{{ $t('lang.dpos_node_title') }}</el-button>
            <el-button @click="showNodeOrReward('reward')" :class="{navtitle: rewardPage}">{{ $t('lang.dpos_reward_title1') }}</el-button>
            <div class="block_height" v-show="rewardPage">{{ $t('lang.dpos_reward_title2') }} {{ $t('lang.dpos_reward_title3') }} <span class="navtitle">{{ firstheight }}</span> {{ $t('lang.dpos_reward_title4') }} <span class="navtitle">{{ lastheight }}</span></div>
          </div>
          <div v-show="nodePage">
            <el-table
              :data="tableData"
              style="width: 100%;">
              <el-table-column
                prop="agent_id"
                :label="$t('lang.dpos_node_table1')"
                width="250">
                <template slot-scope="scope">
                  <router-link tag="a" :to="{path:'/list',query:{nodeid:scope.row.agent_id}}">{{ scope.row.show_agent_id }}
                  </router-link>
                </template>
              </el-table-column>
              <el-table-column
                prop="name"
                :label="$t('lang.dpos_node_table2')">
              </el-table-column>
              <el-table-column
                prop="generated_block_amount72"
                :label="$t('lang.dpos_node_table3')"
                width="150">
              </el-table-column>
              <el-table-column
                prop="amount"
                :label="$t('lang.dpos_node_table4')">
              </el-table-column>
              <el-table-column
                prop="dividend"
                :label="$t('lang.dpos_node_table5')">
              </el-table-column>
              <el-table-column
                prop="last_block_time"
                :label="$t('lang.dpos_node_table6')"
                width="250">
                <template slot-scope="scope">
                  {{ filterDate(scope.row.last_block_time) }}
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div v-show="rewardPage">
            <el-table
              :data="rewardData"
              @sort-change='tableSortChange'
              :row-class-name="tableRowClassName">
              style="width: 100%;">
              <el-table-column
                prop="id"
                :label="$t('lang.dpos_reward_table1')"
                width="100">
              </el-table-column>
              <el-table-column
                prop="name"
                :label="$t('lang.dpos_reward_table2')">
              </el-table-column>
              <el-table-column
                prop="last_season_reward"
                sortable="custom"
                :label="$t('lang.dpos_reward_table3')">
              </el-table-column>
              <el-table-column
                prop="amount"
                :label="$t('lang.dpos_reward_table4')">
              </el-table-column>
              <el-table-column
                prop="current_season_dividend"
                :label="$t('lang.dpos_reward_table5')">
              </el-table-column>
              <el-table-column
                prop="expected_reward"
                sortable="custom"
                :label="$t('lang.dpos_reward_table6')"
                width="250">
              </el-table-column>
            </el-table>
          </div>
        </div>
      </el-main>
    </el-container>
    <Footer/>
  </div>
</template>

<script>
    import Header from '@/components/Header'
    import Footer from '@/components/Footer'
    import pageSize from '@/components/Page'
    import dateFns from 'date-fns'

    export default {
        components: {
            Header,
            Footer,
            pageSize
        },
        data() {
            return {
                tableData: [],
                rewardData: [],
                firstheight: "",
                lastheight: "",
                nodeData: [],
                nodePage: true,
                rewardPage: false,
                sorttype: "auto",
                level: 26,
                count : 0
            }
        },
        computed: {},
        mounted() {
            this.getNodeList()
            this.getRewardList()
        },
        methods: {
            getNodeList(){
                this.$http.get("api/node/agent").then(res => {
                    if (res.data.code === 200){
                        this.tableData = res.data.data.list
                        this.nodeData = res.data.data.node
                    }
                })
            },
            getRewardList(){
                this.$http.post("api/node/reward", {sorttype: this.sorttype}).then(res => {
                    if (res.data.code === 200){
                        this.rewardData = res.data.data.list
                        this.count = this.rewardData.length
                        this.firstheight = res.data.data.first
                        this.lastheight = res.data.data.last
                    }
                })
            },
            filterDate(value, type) {
                if (!value) return ''
                if(value > 0){
                    value = value * 1000
                }
                const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
                return dateFns.format(value, filterType)
            },
            showNodeOrReward(name) {
                if ( name === 'node' ) {
                    this.nodePage = true
                    this.rewardPage = false
                } else if ( name === 'reward' ) {
                    this.nodePage = false
                    this.rewardPage = true
                }else{
                    this.nodePage = true
                    this.rewardPage = false
                }
            },
            tableSortChange(co){
                if (co.prop === "last_season_reward" && co.order === "ascending"){
                    this.sorttype = "season_asc"
                    this.getRewardList()
                } else if (co.prop === "last_season_reward" && co.order === "descending") {
                    this.sorttype = "season_desc"
                    this.getRewardList()
                }else if (co.prop === "expected_reward" && co.order === "ascending") {
                    this.sorttype = "expected_asc"
                    this.getRewardList()
                }else if (co.prop === "expected_reward" && co.order === "descending") {
                    this.sorttype = "expected_desc"
                    this.getRewardList()
                }else {
                    this.sorttype = "auto"
                    this.getRewardList()
                }
            },
            tableRowClassName({row, rowIndex}) {
                if (this.sorttype === "season_desc" || this.sorttype === "expected_desc" ) {
                    if (rowIndex === this.level) {
                        return 'levelclass';
                    }
                }else if (this.sorttype === "auto") {
                    if (rowIndex === this.level) {
                        return 'levelclass';
                    }
                }else {
                    if (rowIndex === (this.count - this.level -2)) {
                        return 'levelclass';
                    }
                }
            }
        }
    }


</script>
<style>
  .levelclass {
    border-bottom: 6px solid #E7E9EF;
  }
  .levelclass td {
    border-bottom: 6px solid #E7E9EF !important;
  }
  .el-main {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: center;
  }
  .el-main .agent-content {
    width: 1200px !important;
    min-width: 1200px;
  }
  .el-main .agent-content .con-title {
    height:60px;
    background:rgba(243,244,247,1);
    border-radius:4px 4px 0 0;
    font-size:18px;
    font-weight:400;
    color:rgba(0,0,0,1);
    line-height:18px;
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 0;
    margin-top: 20px;
    position: relative;
  }
  .el-main .agent-content .con-title .el-button {
    font-size:18px;
    font-weight:400;
    color:rgba(136,136,136,1);
    border: none;
    background: none;
  }
  .el-main .agent-content .con-title .block_height {
    font-size:14px;
    font-weight:400;
    color:rgba(136,136,136,1);
    position: absolute;
    right: 30px;
  }
  .navtitle {
    color:rgba(0,93,166,1) !important;
  }
  .el-main .agent-content .el-table {
    padding: 0 27px;
  }
  .el-main .agent-content .el-table tr {
    height: 60px;
  }
  .el-main .agent-content .el-table thead .cell {
    font-size:16px;
    font-weight:400;
    color:rgba(0,0,0,1);
    padding-right: 0;
  }
  .el-main .agent-content .el-table {
    padding: 0;
  }
  .el-main .agent-content .el-table tr td:first-child {
    padding-left: 27px;
  }
  .el-main .agent-content .el-table tr th:first-child {
    padding-left: 27px;
  }
  .el-main .agent-content .el-table tr td:last-child {
    padding-right: 27px;
  }
  .el-main .agent-content .el-table tr th:last-child {
    padding-right: 27px;
  }
  .el-main .agent-content .el-table tbody .cell {
    font-size:14px;
    font-weight:400;
    color:rgba(0,0,0,1);
    padding-right: 0;
  }

  .el-main .agent-content .con-con {
    background: rgba(255, 255, 255, 1);
    border-radius: 4px;
  }

  .el-main .agent-content .con-con .con-total {
    padding: 0;
  }

  .el-main .agent-content .con-con > .con-total .el-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid rgba(240, 240, 240, 1);
  }

  .el-main .agent-content .con-con > .con-total .el-row .el-col {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 25px 0;
    height: 70px;
    border-left: 4px solid rgba(240, 240, 240, 1);
  }

  .el-main .agent-content .con-con > .con-total .el-row .el-col:first-child {
    border-left: none;
  }

  .el-main .agent-content .con-con > .con-total .el-row .el-col label {
    font-size: 20px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
    height: 35px;
    line-height: 35px;
  }

  .el-main .agent-content .con-con > .con-total .el-row .el-col p {
    font-size: 14px;
    font-weight: 200;
    color: rgba(136, 136, 136, 1);
    height: 35px;
    line-height: 35px;
  }

  .el-main .agent-content .con-con .el-tabs .el-tabs__header {
    display: flex;
    justify-content: center;
    margin-top: 0;
    margin-bottom: 30px;
    border: none;
  }

  .el-main .agent-content .con-con .el-tabs .el-tabs__nav {
    border: none;
  }

  .el-main .agent-content .con-con .el-tabs button {
    width: 144px;
    height: 40px;
    background: rgba(0, 93, 166, 1);
    border-radius: 4px;
    font-size: 14px;
    font-weight: 400;
    color: rgba(255, 255, 255, 1);
    outline: none;
    border: none;
    margin-bottom: 30px;
  }

  .el-main .agent-content .con-con .el-tabs .el-row {
    display: flex;
    flex-direction: column;
    justify-content: left;
    background: rgba(255, 255, 255, 1);
    border-radius: 4px;
    font-size: 16px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
    align-items: center;
  }

  .el-main .agent-content .con-con .el-tabs .el-row .el-col {
    display: flex;
    flex-direction: row;
    justify-content: left;
    height: 60px;
    border-bottom: 1px solid rgba(245, 245, 245, 1);
    align-items: center;
    padding-left: 10px;
  }

  .el-main .agent-content .con-con .el-tabs .el-row label {
    width: 25%;
    text-align: left;
  }
</style>




