<template>
  <div class="page-wrapper">
    <el-pagination
      :current-page="pageData.currentPage"
      :page-size="pageData.pageSize"
      :total="pageData.total"
      :layout="layout"
      background
      @size-change="sizeChange"
      @current-change="currentChange"
    >
    </el-pagination>
  </div>
</template>

<script>

    export default {
        props: {
            pageData: {
                type: Object,
                required: true,
                default: function () {
                    return {
                        currentPage: 1,
                        pageSize: 10,
                        total: 0
                    }
                }
            },
            layout: {
                type: String,
                default: 'total, prev, pager, next, sizes, jumper'
            }
        },
        data() {
            return {}
        },
        methods: {
            currentChange(value) {
                this.$emit('currentChange', value)
            },
            sizeChange(size) {
                this.$emit('sizeChange', size)
            }
        }
    }
</script>

<style scoped>
  .page-wrapper {
    background-color: #fff;
    padding: 10px;
    text-align: right;
  }

  .page-wrapper .el-pagination {
    padding: 0;
  }
</style>



