<script>
  import Header from '@/components/Header'
  import Footer from '@/components/Footer'
  import QRCode from 'qrcodejs2'
  import dateFns from 'date-fns'

  export default {
    components: {
      Header,
      Footer,
    },
    data() {
      return {
        transData: [],
        blockData: [],
        height: '',
        hash: '',
        previous: '',
        next: '',
        blockinfo: false,
        maxHeight: ''

      }
    },
    created() {
      this.height = this.$route.query.height;
      this.hash = this.$route.query.hash;
    },
    watch: {
      $route() {
        this.height = this.$route.query.height;
        this.hash = this.$route.query.hash;
        if (!this.hash) {
          this.getBlockInfoByHeight(this.height)
        } else {
          this.getBlockInfoByHash(this.hash)
        }
      }
    },
    computed: {},
    mounted() {
      this.getMaxHeight()
      if (this.previous === '') {
        this.previous = 0
      }
      if (this.next === '') {
        this.next = this.maxHeight
      }
      if (this.maxHeight === '') {
        this.maxHeight = 0
      }
      if (!this.hash) {
        this.getBlockInfoByHeight(this.height)
      } else {
        this.getBlockInfoByHash(this.hash)
      }

    },
    methods: {
      getBlockInfoByHeight(height) {
        height = parseInt(height)
        this.getMaxHeight()
        this.$http.post("api/block/info", {height: height}).then(res => {
          if (res.data.code === 200) {
            this.blockData = res.data.data.block
            this.previous = this.max(parseInt(res.data.data.block.height) - 1, 0)
            this.next = this.min(parseInt(res.data.data.block.height) + 1, parseInt(this.maxHeight))
            this.transData = res.data.data.list

            setTimeout(() => {
              $(".qrdata").html("")
            }, 500);

          } else {
            this.previous = 0
            this.next = this.maxHeight
          }
        })
      },
      getBlockInfoByHash(hash) {
        this.$http.post("api/block/hash", {hash: hash}).then(res => {
          if (res.data.code === 200) {
            this.blockData = res.data.data.block
            this.previous = this.max(parseInt(res.data.data.block.height) - 1, 0)
            this.next = this.min(parseInt(res.data.data.block.height) + 1, parseInt(this.maxHeight))
            this.transData = res.data.data.list
          }
        })
      },
      getMaxHeight() {
        this.$http.get("api/block/height").then(res => {
          if (res.data.code === 200) {
            this.maxHeight = res.data.data.height
          }
        })
      },
      handelReload(height) {
        this.$router.push({path: '/block', query: {height: height}});
        this.height = height;
        this.getBlockInfoByHeight(this.height);
      },
      max(first, second) {
        return first > second ? first : second;
      },
      min(first, second) {
        return first < second ? first : second;
      },
      handleHidden() {
        if (!this.blockinfo) {
          this.blockinfo = true
        } else {
          this.blockinfo = false
        }
      },
      qrcode(width, height, id, address) {
        new QRCode(id, {
          text: address,
          width: width,
          height: height,
          colorDark: '#333333',
          colorLight: '#ffffff',
          correctLevel: QRCode.CorrectLevel.L
        })
      },
      filterDate(value, type) {
        if (!value) return ''
        if (value > 0) {
          value = value * 1000
        }
        const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
        return dateFns.format(value, filterType)
      },
      subStr(str) {
        if (str === "") {
          return ""
        }
        if (str.length === 34) {
          return str
        }
        str = str.substr(0, 50);
        str += "...";
        return str
      },
      isGoto(str) {
        if (str.indexOf("G") === 0 && str.length === 34){
          return true;
        }else{
          return false;
        }
      }
    }
  }


</script>
