<template>
  <el-container>
    <el-header>
      <el-row type="flex" style="position: relative;z-index: 1000;">
        <el-col>
          <router-link tag="a" :to="{path:'/'}">
            <img src="@/assets/img/logo.png" alt="">
          </router-link>
        </el-col>
        <el-col>
          <router-link tag="a" :to="{path:'/'}">
            <p :class="[this.blockTabs?'acticity-tabs':'']">{{ $t("lang.header_index") }}</p>
          </router-link>
        </el-col>
        <el-col>
          <router-link tag="a" :to="{path:'/agent'}">
            <p :class="[this.posTabs?'acticity-tabs':'']">{{ $t("lang.header_dpos") }}</p>
          </router-link>
        </el-col>
        <el-col>
          <router-link tag="a" :to="{path:'/node'}">
            <p :class="[this.nodeTabs?'acticity-tabs':'']">{{ $t("lang.header_masternodes") }}</p>
          </router-link>
        </el-col>
        <el-col>
          <router-link tag="a" :to="{path:'/token'}">
            <p :class="[this.tokenTabs?'acticity-tabs':'']">{{ $t("lang.header_Token") }}</p>
          </router-link>
        </el-col>
        <el-col>
          <el-input v-model="input" :placeholder="$t('lang.header_search')"
                    @keydown.enter.native="getBlockSearch"></el-input>
          <img src="@/assets/img/search.png" alt="" @click="getBlockSearch">
        </el-col>
      </el-row>
      <div class="lang">
        <div class="default" @mouseenter="enter()">
          <img :src="langimg" alt="" class="lang_log">
          <span>{{ value }}</span>
          <img src="@/assets/img/lang_select.png" alt="">
        </div>
        <div class="lang_list" v-show="lang" @mouseenter="enter()"  @mouseleave="leave()">
          <ul>
            <li v-for="item in langs" @click="langchange(item.icon)">{{ item.name }}</li>
          </ul>
        </div>
      </div>
    </el-header>

  </el-container>
</template>

<script>
  import Cookies from "js-cookie";

  export default {
    components: {},
    data() {
      return {
        type: '',
        search: '',
        input: '',
        blockTabs: true,
        posTabs: false,
        nodeTabs: false,
        tokenTabs: false,
        langimg: Cookies.get("lang_img") || "./static/img/en.png",
        value: Cookies.get("lang_name") || "English",
        langs: {
          en: {
            name: "English",
            icon: "en"
          }
          ,
          zh: {
            name: "简体中文",
            icon: "zh"
          }
        },
        lang: false
      }
    },
    computed: {},
    mounted() {
      this.showTabs()
    },
    methods: {
      getBlockSearch() {
        let search = this.input
        if (!search) {
          this.$message.error(this.$t('lang.header_msg1'));
          return
        }
        this.$http.post("api/block/search", {search: search}).then(res => {
          if (res.data.code === 200) {
            this.type = res.data.data.type
            if (this.type === "block") {
              let height = parseInt(res.data.data.search)
              this.$router.push({path: '/block', query: {height: height}});
            } else if (this.type === "transaction") {
              let txid = res.data.data.search
              this.$router.push({path: '/tx', query: {txid: txid}});
            } else if (this.type === "address") {
              let address = res.data.data.search
              this.$router.push({path: '/address', query: {address: address}});
            } else {
              this.$message.error(this.$t('lang.header_msg2'));
            }
          } else {
            this.$message.error(this.$t('lang.header_msg2'));
          }
        })
      },
      showTabs() {
        if (this.$route.path === "/node") {
          this.blockTabs = false
          this.nodeTabs = true
          this.posTabs = false
          this.tokenTabs = false
        }
        if (this.$route.path === "/agent" || this.$route.path === "/list" || this.$route.path === "/ranking") {
          this.blockTabs = false
          this.nodeTabs = false
          this.tokenTabs = false
          this.posTabs = true
        }
        if (this.$route.path === "/token" || this.$route.path === "/tokendetail" || this.$route.path === "/tokenone") {
          this.blockTabs = false
          this.nodeTabs = false
          this.posTabs = false
          this.tokenTabs = true
        }
      },
      leave() {
        this.lang = false
      },
      enter() {
        this.lang = true
      },
      langchange(lang) {
        for (let index in this.langs) {
          if (index === lang) {
            this.value = this.langs[index].name
            Cookies.set("lang", lang)
            Cookies.set("lang_name", this.value)
            Cookies.set("lang_img", "./static/img/" + lang + ".png")
            console.log("./static/img/" + lang + ".png")
            $('.lang_log').attr("src", "./static/img/" + lang + ".png")
            this.$i18n.locale = lang
          }
        }
        this.lang = false
      }
    }
  }
</script>

<style>
  .lang {
    position: absolute;
    top: 25px;
    display: flex;
    align-items: flex-end;
    height: 80px;
    flex-direction: column;
    width: 97%;
    min-width: 1550px;
    padding-right: 3%;
  }

  .lang .default {
    height: 30px;
    cursor: pointer;
    display: flex;
    align-items: center;
  }

  .lang .default span {
    font-size: 14px;
    color: #ffffff;
    vertical-align: middle;
    display: block;
    height: 30px;
    line-height: 30px;
    width: 60px;
    text-align: left;
  }

  .lang .default img {
    width: 30px;
    height: 30px;
    vertical-align: middle;
  }

  .lang .default img:first-child {
    margin-right: 10px;
  }

  .lang_list {
    background: rgba(255, 255, 255, 1);
    opacity: 1;
    border-radius: 2px;
    width: 100px;
    position: absolute;
    top: 30px;
  }

  .lang_list ul {
    padding: 5px 10px;
    font-size: 14px;
    font-weight: 400;
    color: rgba(32, 38, 64, 1);
    opacity: 1;
  }

  .lang_list ul li {
    line-height: 24px;
    text-align: left;
    cursor: pointer;
  }

  .lang_list ul:hover {
    display: block;
  }

  .lang_list ul li:hover {
    color: rgba(0, 93, 166, 1);
  }

  .el-header {
    height: 78px !important;
    line-height: 78px;
    background-color: rgba(0, 93, 166, 1);
    display: flex;
    justify-content: center;
  }

  .el-header .el-row {
    width: 1200px;
    min-width: 1200px;
  }

  .el-header .el-row .el-col {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .el-header .el-row .el-col a {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
  }

  .el-header .el-row .el-col a > img {
    width: 170px;
    height: 48px;
  }

  .el-header .el-row .el-col a > p {
    font-size: 18px;
    font-weight: 400;
    color: rgba(135, 166, 203, 1);
  }

  .el-header .el-row .el-col > img {
    width: 28px;
    height: 28px;
    position: absolute;
    right: 25px;
    cursor: pointer;
  }

  .el-header .el-row .el-col input {
    width: 600px;
    height: 48px;
    background: none;
    border: 1px solid rgba(95, 147, 198, 1);
    position: relative;
    color: #ffffff;
  }

  .acticity-tabs {
    color: rgba(255, 255, 255, 1) !important;
  }

</style>



